@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@300;400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
  font-family: "Public Sans", sans-serif;
}

.about {
  padding: 7rem;
  background-color: #f2f6f9;
}

.about__header > h2 {
  margin-bottom: 12px;
  font-size: 4rem;
  font-weight: 400;
  text-align: center;
  color: hsl(233, 26%, 24%);
}

.about__header > p {
  margin-bottom: 36px;
  font-size: 1.6rem;
  font-weight: 300;
  text-align: center;
  line-height: 26px;
  color: hsl(233, 8%, 62%);
}

.about__cards {
  padding: 0 3rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
}

.about__cards > .card {
  text-align: center;
  margin: 1.5rem auto;
}

.card__image {
  margin-bottom: 2rem;
}

.card > h3 {
  margin-bottom: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  text-align: center;
  color: hsl(233, 26%, 24%);
}

.card > p {
  margin-bottom: 2rem;
  font-size: 1.6rem;
  font-weight: 400;
  text-align: center;
  line-height: 26px;
  color: hsl(233, 8%, 62%);
}

.articles {
  padding: 7rem 3rem;
  background-color: hsl(0, 0%, 98%);
}

.articles__cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 3rem;
  gap: 3rem;
  place-items: center;
}

.articles h2 {
  margin-bottom: 4rem;
  font-size: 4rem;
  font-weight: 400;
  text-align: center;
  color: hsl(233, 26%, 24%);
}

.article__card {
  min-width: 320px;
  max-width: 380px;
  min-height: 430px;
  margin: 0 2rem 3rem;
  display: grid;
  grid-template-rows: 1fr 150px;
  background-color: hsl(0, 0%, 100%);
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  box-shadow: 0 0 18px rgba(0, 0, 0, 0.1);
}

.article__card:hover {
  cursor: pointer;
}

.article__card:hover .article-card__image img {
  opacity: 0.8;
  -webkit-filter: contrast(120%);
          filter: contrast(120%);
}

.article-card__image img {
  min-width: 320px;
  max-width: 380px;
  height: 100%;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  object-fit: cover;
  transition: all 0.3s ease;
}

.card__body {
  padding: 1.6rem;
}

.article__author {
  font-size: 1.2rem;
  font-weight: 300;
  color: hsl(233, 8%, 62%);
  margin-bottom: 1.2rem;
}

.article__title {
  font-size: 1.6rem;
  font-weight: 400;
  color: hsl(233, 26%, 24%);
  margin-bottom: 1.2rem;
}

.article__intro {
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 20px;
  color: hsl(233, 8%, 62%);
}

.btn {
  width: 190px;
  height: 42px;
  border-radius: 24px;
  font-size: 1.8rem;
  font-weight: 400;
  color: hsl(0, 0%, 98%);
  background: rgb(49, 211, 92);
  background: linear-gradient(
    90deg,
    rgba(49, 211, 92, 1) 0%,
    rgba(43, 183, 218, 1) 100%
  );
  outline: none;
  border: none;
}

.btn:hover {
  color: hsl(0, 0%, 98%);
  opacity: 0.7;
  cursor: pointer;
}

.footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(92 91 160);
  padding: 0 3rem;
  padding-top: 3rem;
}

.footer > div {
  margin-bottom: 3rem;
}

.footer__logo-social > img {
  width: 205px;
  margin-bottom: 3rem;
}

.social {
  display: flex;
  justify-content: space-between;
}

.social img {
  height: 26px;
}

.footer__links div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__links a {
  margin-bottom: 1.2rem;
  font-size: 1.8rem;
  color: hsl(0, 0%, 98%);
  text-decoration: none;
}

.footer__logo-social > img:hover,
.footer__links a:hover {
  cursor: pointer;
}

.footer__cta {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__cta p {
  margin: 3rem 0;
  font-size: 1.6rem;
  font-weight: 700;
  line-height: 20px;
  color: hsl(233, 8%, 62%);
}

@media only screen and (min-width: 1024px) {
  .footer {
    flex-direction: row;
    justify-content: space-between;
    padding-top: 2rem;
  }

  .footer__links {
    width: 40%;
    display: flex;
  }

  .footer__links div {
    margin-right: 4rem;
    align-items: flex-start;
  }

  .footer__cta {
    align-items: flex-end;
    margin-top: 2rem;
  }
}

.hero {
  width: 100%;
  height: calc(100vh - 64px);
}

.hero__image {
  width: 100%;
  min-width: 320px;
  height: 50vh;
  background-image: url(/easyBank-landingPage/static/media/image-mockups.48d35b17.png),
    url(/easyBank-landingPage/static/media/bg-intro-mobile.c2ed38d0.svg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.hero__cta {
  width: 100%;
  height: calc(50vh - 56px);
  padding: 3rem;
  max-width: 724px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.hero__cta > h1 {
  margin-bottom: 12px;
  font-size: 4rem;
  font-weight: 300;
  text-align: center;
  color: hsl(233, 26%, 24%);
}

.hero__cta > p {
  margin-bottom: 36px;
  font-size: 1.6rem;
  font-weight: 300;
  text-align: center;
  line-height: 26px;
  color: hsl(233, 8%, 62%);
}

.hero__cta-btn {
  text-align: center;
}

@media only screen and (min-width: 1024px) {
  .hero {
    display: flex;
    height: 50vh;
  }

  .hero__cta {
    order: 1;
  }

  .hero__image {
    order: 2;
    z-index: 2;
  }
}

nav {
  padding: 8px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 1px solid rgba(233, 233, 233, 0.8);
}

.nav-container {
  width: 100%;
  height: 56px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: sticky;
  padding: 0 3rem;
  padding-top: 6px;
  background-color: #fff;
  box-shadow: 0 0 18px rgba(0, 0, 0, 0.1);
  z-index: 20;
}

.nav {
  width: 100%;
  height: 64px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 9px 0;
  z-index: 2;
}

.nav__toggle > i {
  font-size: 2.2rem;
  color: #7b7c7e;
}

.nav__toggle-icon {
  width: 16px;
  height: 16px;
}

.nav__toggleable {
  width: 290px;
  display: none;
  position: absolute;
  top: 70px;
}

.nav__toggleable--show {
  display: flex;
  z-index: 2;
}

nav {
  margin: 16px;
  padding: 18px;
  border-radius: 8px;
  background-color: rgb(255, 255, 255);
  border: 1px solid #bbb;
}

.nav__menu {
  margin-top: 8px;
  display: flex;
  flex-direction: column;
}

.nav__menu > a {
  height: 40px;
  font-size: 1.8rem;
  font-weight: 400;
  color: hsl(233, 26%, 24%);
  text-decoration: none;
  text-align: center;
}

@media only screen and (min-width: 1024px) {
  .nav-container {
    flex-direction: row;
    justify-content: space-between;
  }

  .nav {
    width: 30%;
  }

  .nav__logo:hover {
    cursor: pointer;
  }

  .nav__toggle {
    display: none;
  }

  .nav__toggleable {
    width: 70%;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
    position: relative;
    background-color: transparent;
    border: none;
    border-radius: 0;
    top: 0;
  }

  .nav__menu {
    flex-direction: row;
  }

  .nav__menu a {
    margin-right: 3rem;
  }

  .nav__menu > a:hover {
    border-bottom: 2px solid #31d35c;
    cursor: pointer;
  }
}

